.error {
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 13px;
    color: red
}

.form {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 5%;
}

.title {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding-bottom: 5%;
    padding-top: 9%;
    font-size: 40px;
    color: whitesmoke;
    background-color: black;
}

.input-container {
    display: flex;
    flex-wrap: wrap;
    padding: 2%;
    border: 2px solid grey;
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 9px;
    background-color: lightgrey;
}

:hover.input-container {
    opacity: 0.9;
    transition-duration: 1s;
    border-radius: 25px;
}

:hover.button-container {
    transition-duration: 2s;
    opacity: 0.5;
    box-shadow: inset 0 0 0 100px black;
    transform: scale(0.9);
    border-radius: 9px;
}

.button-container {
    display: flex;
    margin-left: 20%;
    margin-right: 20%;
    justify-content: center;
   /* background-color: black;*/
}

.input {
    display: flex;
    margin-left: 20px;
    border: solid grey;
    background-color: snow;
}

.input:focus {
    border-radius: 9px;
}
