.MessageForm {
    display: flex;
    background: #ccc;
    width: 100%;
  }
  
  .MessageForm .input-container {
    display: flex;
    flex: 1;
    margin: 1px;
    width: 100%
  }
  
  .MessageForm .button-container {
    display: flex;
    flex: 0 0 6em;
    margin: 1px 1px 1px 0;
    background-color: burlywood;
  }

  .MessageForm .button-container {
    color: brown;
  }
  
  .MessageForm input, .MessageForm button {
    background: #fff;
    border-radius: 3px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: inherit;
    height: 100%;
    outline: none;
    width: 100%;
    display: flex;
  }
  
  .MessageForm input {
    display: flex;
    padding: 0 0 0 4px;
  }
  