.Message {
    line-height: 2em;
    padding-right: 30%;
    text-orientation: upright;
  }
  
  .Message.log {
    color: #bbb;
  }
  
  .Message .author {
    font-weight: bold;
    color: #888;
    margin: 0 5px 0 0;
  }
  
  .Message.me {
    color: #b6191e;
    padding-left: 50%;
    display: flex;
    flex-direction: column;
  }